import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueResource from 'vue-resource'

Vue.use(VueResource);


//https://segmentfault.com/q/1010000008009082
Vue.http.interceptors.push((request, next) => {
    let description = request.url.description;
    request.url = request.url.path
    next((response) => {
        if (403 === response.status) {
            //  response.body  json status  200  then()  404 
            window.location.href = '/web#/login';
        } else if (302 === response.status) {
            // 302 ,
            window.location.href = '/web#/'
        } else if (200 === response.status) {
            if (response.body.status !== 1) {
                window.console.error(`${description}: ${response.body.msg}`);
            } else {
                //
            }
        } else {
            window.console.error(`${description}(stuats code !=200): ${response.status}`);
        }
        return response
    })
})

new Vue({
    router,
    store,
    render: h => h(App),
}).$mount('#app')




